body {
  font-family: 'Montserrat', sans-serif;
  background: rgba(255, 255, 255, 0.85);
  font-weight: 300;
  letter-spacing: 1.2px;
  color: #333;
}
h3 {
  font-size: 1.5em;
  margin: 50px 0 0;
  padding: 0;
  letter-spacing: 1.75px;
  text-align: center;
  position: relative;
}
h3:after {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 0;
  z-index: -10;
  content: '';
  width: 200px;
  height: 10px;
  background: yellow;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  padding: 10px 0;
  font-size: 1.2em;
  list-style: none;
}
.App {
  width: 700px;
  margin: 0 auto;
  padding: 20px;
  height: 100%;
}
.header {
  text-align: center;
  position: relative;
  max-width: 800px;
  margin: 10% auto 0;
  font-size: 4em;
}
.header h1 {
  padding-bottom: 0;
  margin-bottom: 0;
}
.header .header-sub {
  padding-bottom: 15px;
  font-size: 0.8em;
}
.header .header-current {
  font-size: 0.5em;
  margin-bottom: 50px;
}
.projects li {
  margin: 10px 0;
  padding: 0;
}
.projects li div {
  height: 500px;
  background-size: cover;
  background-position: top;
}
.socialLinks ul,
.skillsList ul,
.contact ul {
  text-align: center;
}
.socialLinks li ,
.skillsList li,
.contact li {
  display: inline-block;
  float: none;
  margin: 20px 50px;
}
.socialLinks li a {
  text-decoration: none;
  color: #333;

}
.contact li {
  font-size: 1.5em;
  font-weight: bold;
}
.contact li a {
  background-color: none;
  color: #333;
  padding: .5em;
  text-decoration: none;
  font-weight: bold;
}

@media only screen and (max-width: 768px) {
  body {
    padding: 0;
    margin: 0;
  }
  .App {
    width: 100%;
    padding: 0;
  }
  ul {
    margin: 0;
    padding: 0;
  }
  .projects li {
    width: 100%;
  }
  .projects a {
    top: -30px;
    left: 75%;
  }
  .header {
    font-size: 2.2em;
  }
  .contact li {
    font-size: 1em;
  }
}

@media only screen and (max-width: 365px) {
  body {
    width: 100%;
    margin: 0;
  }
  .App {
    width: 100%;
  }
  ul {
    margin: 0;
    padding: 0;
  }
  .projects li {
    margin-left: 0;
    padding-left: 0;
  }
  .projects a {
    top: 0;
    left: 0;
  }
  .header {
    font-size: 2.2em;
  }
  .contact li {
    font-size: 1em;
  }
}



